.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.app-container {
  text-align: center;
  padding: 20px;
}

.image-container img {
  width: 300px;
  height: 300px;
  object-fit: cover;
}

.quiz-container button {
  margin: 5px;
}

.options button {
  padding: 10px 20px;
  font-size: 16px;
}

.fact-container p {
  font-style: italic;
}

